.fields-row {
  display: flex;
  align-items: center;
  margin-left: -5px;
  margin-right: -5px;
  &__item {
    display: flex;
    align-items: center;
    padding-left: 5px;
    padding-right: 5px;
  }
  &__name {
    margin-right: 5px;
    color: $gray-dark;
    font-weight: bold;
  }
}
